// mealComponent.js
import React, { useEffect, useState, useRef, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import useStore from "../../commonStore";
import { supbaseWrapper } from "../../supabaseClient";
import { toast } from "react-toastify";

export const useMealData = (surveyId, isWeekly = false) => {
  // const { onboardingValue } = useStore();
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [selectedDay, setSelectedDay] = useState(0);
  const headerRef = useRef(null);
  const scrollAreaRef = useRef(null);
  const { getMealPlan } = supbaseWrapper();

  useEffect(() => {
    const fetchData = async () => {
      if (!surveyId) {
        console.error("surveyId ");
        return;
      }

      try {
        // API meal plan  
        const response = await getMealPlan(surveyId);
        console.log(response); // API    
        if (response.error) {
          throw new Error(response.error.message);
        }
        const mealPlanData = response.data || {};
        console.log(mealPlanData); //    

        //  
        const sortedDates = Object.keys(mealPlanData).sort();

        let mealList = sortedDates.map((date) => {
          const dayMeals = ["breakfast", "lunch", "dinner"]
            .map((mealType) => {
              const meal = mealPlanData[date][mealType];
              if (meal) {
                if (meal.type === "") {
                  return {
                    mealType:
                      mealType === "breakfast"
                        ? ""
                        : mealType === "lunch"
                        ? ""
                        : "",
                    dataType: "",
                    name: "",
                  };
                } else if (meal.details) {
                  return {
                    id: meal.details.id,
                    dataType: meal.type,
                    image: meal.details.img_url,
                    name: meal.details.summary || meal.details.name,
                    title: meal.details.title,
                    mealType:
                      mealType === "breakfast"
                        ? ""
                        : mealType === "lunch"
                        ? ""
                        : "",
                    category: meal.details.category || "",
                    brandName: meal.details.brand_name,
                    price: meal.details.price,
                    ingredients: meal.details.ingredients || "",
                  };
                }
              }
              return null;
            })
            .filter((meal) => meal !== null);
          return {
            date: date,
            meals: dayMeals,
            dayMealId: mealPlanData[date]['dayMealId'],
          };
        });
        setData(mealList);
      } catch (error) {
        console.error("    : ", error);
        toast.error("   .");
      }
    };
    fetchData();
  }, [surveyId]);

  const handleMealClick = useCallback(
    (id, dataType) => {
      console.log(`Navigating with id: ${id}`);
      if (dataType === "") {
        navigate(`/recipeDetail`, { state: { id } });
      } else if (dataType === "") {
        navigate(`/productDetail`, { state: { id } });
      } else if (dataType === "") {
        return;
      } else {
        toast.error(`   !`);
        console.error(`  . (dataType: ${dataType})`);
      }
    },
    [navigate]
  );

  return {
    data,
    selectedDay,
    setSelectedDay,
    handleMealClick,
    headerRef,
    scrollAreaRef,
  };
};

